import drawer from 'drawerjs';
import ProgressCard from 'app/components/ProgressCard';
import { PaperPlanIcon } from 'app/images/icons';
import en from 'app/locales/en';
import dayjs from 'dayjs';
import { useEffect, useState } from 'react';
import { env } from 'utils/constants';
import monday from 'utils/mondaySdk';

function ReadyForSubmission() {
  const [currentValue, setCurrentValue] = useState(0);
  const [totalValue, setTotal] = useState({ value: 0 });

  const updateTotal = (value) => {
    const currentDate = dayjs().format();
    let total = {
      value,
      expiry: currentDate,
    };
    const storedValue = localStorage.getItem('readyForSubmissionTotal');
    if (!storedValue) {
      localStorage.setItem('readyForSubmissionTotal', JSON.stringify(total));
      return setTotal(total);
    }
    const parsedValue = JSON.parse(storedValue);
    if (dayjs(parsedValue.expiry).isAfter(dayjs().subtract(1, 'day'))) {
      total = parsedValue;
    } else {
      localStorage.setItem('readyForSubmissionTotal', JSON.stringify(total));
    }
    return setTotal(total);
  };
  const getData = () => {
    const me = drawer.get('userName');
    const currentDate = dayjs().format('YYYY-MM-DD');
    monday.api(`query {
      boards(ids: [${env.boards.deals}]) {
        readyForSubmission: groups(ids: ["${env.pages.readyForSubmission}"]) {
          items_page(
            query_params:{
              rules: [
                { column_id: "${env.columns.followUpDeals}", compare_value: ["${currentDate}", "${currentDate}"], operator:between},
                { column_id: "${env.columns.agent}", compare_value: "${me}", operator:contains_text}
              ]
              operator:and
            }
          ) {
            items {
              id
            }
          }
        }
      }
    }`).then((res) => {
      const value = res?.data ? res.data.boards[0]
        .readyForSubmission[0].items_page.items.length : 0;
      setCurrentValue(value);
      updateTotal(value);
    });
  };
  useEffect(() => {
    getData();
    const intervalId = setInterval(getData, (1000 * env.intervalTime));
    return () => {
      clearInterval(intervalId);
    };
  }, []);
  return (
    <ProgressCard
      value={currentValue}
      total={totalValue.value}
      title={en.Cards.progess.readySubmission.title}
      subTitle={en.Cards.progess.readySubmission.subtitle}
      color="#52B975"
      icon={<PaperPlanIcon />}
    />
  );
}

export default ReadyForSubmission;
